function getBetterSplitterRegex(splitter) {
    const betterSplitter = `(${splitter})`;
    return new RegExp(betterSplitter, 'gi');
}

export function getParts(splitter, str) {
    if (!splitter) {
        return [str];
    }
    const regSplitter = getBetterSplitterRegex(splitter);
    return str.split(regSplitter);
}
