<template>
  <div>
      <v-container class="searcher-wrapper">
          <v-btn ref="search_btn"
                 @click="copyToClipboard"
                 large
                 class="mr-10 d-none d-md-block"
                 depressed tile
          >Copy output to clipboard</v-btn>
          <Searcher :searcher-string="searcherString"
                    @searcher-string="onSearcherString"
          ></Searcher>
      </v-container>
      <v-container>
          <SearchResults v-if="turoyoCorpusLinesMatching"
                         :lines-matching="turoyoCorpusLinesMatching"
                         :searcher-string="searcherString"
                         ref="search_results"
          ></SearchResults>
      </v-container>
  </div>
</template>

<script>
import Searcher from '@/components/turoyoCorpusSearch/Searcher';
import SearchResults from '@/components/turoyoCorpusSearch/SearchResults';
import appBarTitleMixin from '@/mixins/appBarTitleMixin';
import copyToClipboardMixin from '@/mixins/copyToClipboardMixin';
import { mapGetters } from 'vuex';

export default {
    name: "TuroyoCorpusSearch",
    mixins: [
        appBarTitleMixin,
        copyToClipboardMixin
    ],
    components: {
        Searcher,
        SearchResults,
    },
    data() {
        return {
            appBarTitle: 'Turoyo Corpus Search',
            searcherString: '',
        }
    },
    computed: mapGetters(['turoyoCorpusLinesMatching']),
    created() {
        this.$store.commit('initializeTuroyoCorpusData');
        this.$store.commit('setTuroyoCorpusSearcherString', '');

    },
    mounted() {
        if (this.$route.query.search) {
            this.onSearcherString(this.$route.query.search);
        }
    },
    watch: {
        searcherString(value, prev) {
            if (value !== prev || !value) {
                this.setQuery();
            }
        }
    },
    methods: {
        onSearcherString(searcherString) {
            this.searcherString = searcherString;
            this.$store.commit('setTuroyoCorpusSearcherString', this.searcherString);
        },
        setQuery() {
            const query = this.searcherString ? { search: this.searcherString } : {};
            if (_.isEqual(query, this.$route.query)) {
                return;
            }
            this.$router.push({ query });
        }
    }
}
</script>

<style scoped lang="scss">
.searcher-wrapper {
    display: flex;
    justify-content: center;
}
</style>
