<template>
    <div>
        <span v-for="(part, idx) in parts" :key="idx" :style="style(idx)">{{ part }}</span>
    </div>
</template>

<script lang="ts">
import { getParts } from '../../services/splitterUtils';

export default {
    name: 'GrepLine',
    props: {
        str: {
            type: String,
        },
        splitter: {
            type: String,
        },
        color: {
            type: String,
            default: 'black',
        },
        emphColor: {
            type: String,
            default: 'red',
        },
        bold: {
            type: Boolean,
            default: true,
        }
    },
    computed: {
        parts() {
            return getParts(this.splitter, this.str);
        },
        normalClass() {
            return {
                color: this.color,
            }
        },
        selectionClass() {
            return {
                color: this.emphColor,
                fontWeight: this.bold ? 'bold' : 'normal',
            }
        },
    },
    methods: {
        style(idx) {
            return idx % 2 ? this.selectionClass : this.normalClass;
        }
    }
}
</script>

