<template>
    <div class="searcher d-flex align-center flex-column">
        <div class="searchers d-flex">
            <KeyboardIcon
                @click="activateKeyboard"
            ></KeyboardIcon>
            <v-text-field
                autocomplete="off"
                outlined dense clearable
                label="Turoyo search string"
                color="primary"
                :rules="rules"
                v-model="searcherInternal"
                @blur="search"
                @keyup.enter="search"
            ></v-text-field>
            <v-btn ref="search_btn"
                   :disabled="!searcherInternal"
                   @click="search"
                   large
                   depressed tile
            >Search</v-btn>
        </div>

        <v-dialog
            v-model="isKeyboardOpen"
            hide-overlay fullscreen
        >
            <Keyboard
                v-if="isKeyboardOpen"
                :buttons="buttonsArray"
                :value="searcherInternal"
                input-name="Turoyo search string"
                @close="isKeyboardOpen = false"
                @submit="onKeyboardSubmit"
            ></Keyboard>
        </v-dialog>

    </div>
</template>

<script lang="ts">

import Keyboard from '../common/Keyboard.vue';
import { turoyoButtons } from '../../data/turoyoButtons';
import KeyboardIcon from '../common/KeyboardIcon.vue';


export default {
    name: 'Searcher',
    props: ['searcherString'],
    components: {
        Keyboard,
        KeyboardIcon
    },
    data() {
        return {
            inputElement: null,
            searcherInternal: '',
            rules: [
                (str) => {
                    if (!str) {
                        return 'Search string should not be empty'
                    } else {
                        return true;
                    }
                }
            ],
            buttonsArray: turoyoButtons,
            isKeyboardOpen: false,
        }
    },
    watch: {
        searcherString(value) {
            this.searcherInternal = value;
        },
    },
    methods: {
        search() {
            this.$emit('searcher-string', this.searcherInternal);
        },
        activateKeyboard() {
            this.isKeyboardOpen = true;
        },
        onKeyboardSubmit(value) {
            this.searcherInternal = value;
        },
    },
    async mounted() {
        await this.$nextTick();
        this.inputElement = document.querySelector('.v-text-field input');
    },
}
</script>

<style lang="scss" scoped>
.searchers {
    & > *:not(:last-child) {
        margin-right: 20px;
    }
}

.keyboard {
    * {
        text-transform: none !important;
    }

    .v-btn {
        padding: 0 !important;
    }

    .v-btn__content {
        min-width: 0 !important;
    }
}

.v-input__slot {
    display: flex;
    flex-direction: column;
}
</style>
