import { copyToClipboard } from '@/services/copyToClipboard';

export default {
    data() {
        return {
            searchResultsElement: null,
        }
    },
    methods: {
        copyToClipboard() {
            if (!this.searchResultsElement) {
                return;
            }
            const contents = this.searchResultsElement.innerHTML || this.searchResultsElement.$el.innerHTML;
            if (contents) {
                copyToClipboard(contents);
            }
        },
    },
    async mounted() {
        await this.$nextTick();
        this.searchResultsElement = this.$refs.search_results;
    }
}
